.light-theme {\r\n  --wm-bg0-color: rgb(240, 240, 240);\r\n  --wm-bg1-color: rgb(229, 229, 229);\r\n  --wm-bg2-color: rgb(219, 219, 219);\r\n  --wm-bg2-5-color: rgb(215, 215, 215);\r\n  --wm-bg3-color: rgb(211, 211, 211);\r\n  --wm-bg4-color: rgb(195, 195, 195);\r\n  --wm-bg5-color: rgb(182, 182, 182);\r\n  --wm-bg5-5-color: rgb(170, 170, 170);\r\n  --wm-bg6-color: rgb(158, 158, 158);\r\n  --wm-bg6-4-color: rgb(150, 150, 150);\r\n  --wm-bg7-color: rgb(120, 120, 120);\r\n  --wm-bg7-5-color: rgb(110, 110, 110);\r\n  --wm-bg8-color: rgb(102, 102, 102);\r\n  --wm-bg8-5-color: rgb(95, 95, 95);\r\n  --wm-bg9-color: rgb(78, 78, 78);\r\n  --wm-bg10-color: rgb(43, 43, 43);\r\n  --wm-bg11-color: rgb(30, 30, 30);\r\n}\r\n\r\n// :root {\r\n//   --wm-bg0-color: rgb(240, 240, 240);\r\n//   --wm-bg1-color: rgb(229, 229, 229);\r\n//   --wm-bg2-color: rgb(219, 219, 219);\r\n//   --wm-bg2-5-color: rgb(215, 215, 215);\r\n//   --wm-bg3-color: rgb(211, 211, 211);\r\n//   --wm-bg4-color: rgb(195, 195, 195);\r\n//   --wm-bg5-color: rgb(182, 182, 182);\r\n//   --wm-bg5-5-color: rgb(170, 170, 170);\r\n//   --wm-bg6-color: rgb(158, 158, 158);\r\n//   --wm-bg6-4-color: rgb(150, 150, 150);\r\n//   --wm-bg7-color: rgb(120, 120, 120);\r\n//   --wm-bg7-5-color: rgb(110, 110, 110);\r\n//   --wm-bg8-color: rgb(102, 102, 102);\r\n//   --wm-bg8-5-color: rgb(95, 95, 95);\r\n//   --wm-bg9-color: rgb(78, 78, 78);\r\n//   --wm-bg10-color: rgb(43, 43, 43);\r\n//   --wm-bg11-color: rgb(30, 30, 30);\r\n// }\r\n\r\n\r\n\r\n$wm-bg0-color: var(--wm-bg0-color);\r\n$wm-bg1-color: var(--wm-bg1-color);\r\n$wm-bg2-color: var(--wm-bg2-color);\r\n$wm-bg2-5-color: var(--wm-bg2-5-color);\r\n$wm-bg3-color: var(--wm-bg3-color);\r\n$wm-bg4-color: var(--wm-bg4-color);\r\n$wm-bg5-color: var(--wm-bg5-color);\r\n$wm-bg5-5-color: var(--wm-bg5-5-color);\r\n$wm-bg6-color: var(--wm-bg6-color);\r\n$wm-bg6-4-color: var(--wm-bg6-4-color);\r\n$wm-bg7-color: var(--wm-bg7-color);\r\n$wm-bg7-5-color: var(--wm-bg7-5-color);\r\n$wm-bg8-color: var(--wm-bg8-color);\r\n$wm-bg8-5-color: var(--wm-bg8-5-color);\r\n$wm-bg9-color: var(--wm-bg9-color);\r\n$wm-bg10-color: var(--wm-bg10-color);\r\n$wm-bg11-color: var(--wm-bg11-color);\r\n\r\n\r\n\r\n\r\n// 1 -> 6 : 2.13\r\n// 2 -> 6 : 1.93\r\n// 3 -> 6 : 1.78\r\n// 3 -> 6.3 : 2.08\r\n// 3 -> 7 : 2.94\r\n\r\n// 6 -> 10: 5.27\r\n\r\n// 1 -> 10: 11.25\r\n\r\n\r\n\r\n\r\n$border-color: $wm-bg6-color;\r\n\r\n@mixin defaultTextColor{\r\n  color: $wm-bg9-color;\r\n}\r\n\r\n\r\n\r\n@mixin wm-button-states {\r\n  border-style: solid;\r\n  font-size: 14px;\r\n  font-family: 'Spartan', sans-serif;\r\n  // background-color: transparent;\r\n\r\n  &:focus {\r\n    color: $wm-bg9-color;\r\n    background-color: transparent;\r\n  }\r\n\r\n  &:hover {\r\n    color: $wm-bg10-color;\r\n    background-color: $wm-bg5-color;\r\n    border-color: $wm-bg5-color;\r\n  }\r\n\r\n  &:focus {\r\n    outline: none;\r\n    border-color: $wm-bg8-color;\r\n  }\r\n\r\n  &:active {\r\n    color: $wm-bg11-color;\r\n    background-color: $wm-bg1-color;\r\n    border-color: $wm-bg10-color;\r\n  }\r\n\r\n}\r\n","@use \"colors\" as *;\r\n\r\n.editor-page {\r\n  height: 100%;\r\n  box-sizing: border-box;\r\n  padding: 3px;\r\n\r\n  min-height: 100%;\r\n  max-height: 100%;\r\n\r\n  .editor-page-content {\r\n    height: 100%;\r\n    box-sizing: border-box;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    border: 2px solid $border-color;\r\n    border-radius: 6px;\r\n    \r\n    .editor-page-monaco-container {\r\n      flex-grow: 1;\r\n      height: 100%;\r\n      overflow: hidden;\r\n      padding: 2px;\r\n    }\r\n\r\n    .editor-page-separator {\r\n      height: 0px;\r\n      border-top: 2px solid $border-color;\r\n    }\r\n\r\n    .editor-page-control-bar {\r\n      display: flex;\r\n      padding: 4px;\r\n      align-items: center;\r\n      @include defaultTextColor;\r\n    }\r\n  }\r\n}\r\n","@use \"colors\" as *;\r\n\r\n.horizontal-resize {\r\n  box-sizing: border-box;\r\n  height:100%;\r\n\r\n  >div {\r\n    box-sizing: border-box;\r\n    height: 100%;\r\n\r\n    cursor: ew-resize;\r\n\r\n    border-style: solid;\r\n    border-width: 1px 1px 1px 1px;\r\n    padding: 1px 1px 1px 1px;\r\n    border-radius: 4px;\r\n\r\n\r\n    border-color: $wm-bg3-color;\r\n    background-color: $wm-bg3-color;\r\n\r\n    >div {\r\n      box-sizing: border-box;\r\n      height: 100%;\r\n\r\n      border-style: solid;\r\n      border-width: 1px 1px 1px 1px;\r\n      border-radius: 2px;\r\n\r\n      width: 100%;\r\n      border-color: $wm-bg4-color;\r\n      background-color: $wm-bg5-color;\r\n\r\n      transition: inherit;\r\n      transition-delay: inherit;\r\n    }\r\n\r\n    &:hover {\r\n      transition: 0.2s;\r\n      transition-delay: 0.2s;\r\n\r\n      border-color: $wm-bg5-color;\r\n      background-color: $wm-bg5-5-color;\r\n      div {\r\n        border-color: $wm-bg6-color;\r\n        background-color: $wm-bg6-4-color;\r\n      }\r\n    }\r\n    &:active {\r\n      border-color: $wm-bg7-color;\r\n      background-color: $wm-bg7-5-color;\r\n      div {\r\n        border-color: $wm-bg8-color;\r\n        background-color: $wm-bg8-5-color;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n","@use \"colors\" as *;\r\n\r\n.wm-button {\r\n  font-size: 14px;\r\n  font-family: 'Spartan', sans-serif;\r\n  border-radius: 6px;\r\n  padding: 3px 10px;\r\n\r\n  border-width: 2px;\r\n  border-color: $border-color;\r\n\r\n  @include defaultTextColor;\r\n  background-color: transparent;\r\n\r\n  @include wm-button-states;\r\n\r\n\r\n}","@use \"colors\" as *;\r\n\r\n\r\n.sidebar-container {\r\n  padding: 3px;\r\n  box-sizing: border-box;\r\n  height: 100%;\r\n  min-height: 100%;\r\n  max-height:100%;\r\n\r\n  .sidebar-scrollable-content {\r\n    height: 100%;\r\n    padding-right: 4px;\r\n\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 8px;\r\n    overflow-y: scroll;\r\n    overflow-x: hidden;\r\n\r\n    scrollbar-width: thin;\r\n    scrollbar-color: $wm-bg3-color transparent;\r\n\r\n    .sidebar-panel {\r\n      .sidebar-panel-header {\r\n        border: 2px solid $border-color;\r\n        display: flex;\r\n\r\n        border-radius: 6px;\r\n        &.wm-expanded { border-radius: 6px 6px 0px 0px; }\r\n\r\n        @include defaultTextColor;\r\n        background: $wm-bg2-color;\r\n\r\n        padding-inline: 0px;\r\n        font-size: 14px;\r\n        user-select: none;\r\n\r\n        .sidebar-panel-header-caption {\r\n  \r\n          display: flex;\r\n          flex-grow: 1;\r\n          text-align: left;\r\n          @include defaultTextColor;\r\n          background-color: transparent;\r\n          border: 1px solid transparent;\r\n          border-radius: 4px;\r\n          padding: 2px;\r\n\r\n          @include wm-button-states;\r\n\r\n          :nth-child(2) {\r\n            flex-grow: 1;\r\n            padding-left: 3px;\r\n            padding-right: 12px;\r\n          }\r\n        }\r\n\r\n\r\n        &:hover .sidebar-panel-header-options {\r\n          @include defaultTextColor;\r\n        }\r\n\r\n        .sidebar-panel-header-options {\r\n          background-color: transparent;\r\n          border: 1px solid transparent;\r\n          border-radius: 4px;\r\n          padding: 2px;\r\n          color: transparent;\r\n          @include wm-button-states;\r\n        }\r\n      }\r\n\r\n      .sidebar-panel-content {\r\n        max-width: 100%;\r\n        box-sizing: border-box;\r\n        font-size: 14px;\r\n        line-height: 18px;\r\n        @include defaultTextColor;\r\n  \r\n        border: 2px solid $border-color;\r\n        border-top: 0px;\r\n        border-radius: 0px 0px 6px 6px;\r\n        overflow: hidden;\r\n        padding: 4px;\r\n        margin: 0px;\r\n        &.wm-collapsed {\r\n          display: none;\r\n        }\r\n\r\n        .sidebar-panel-chart {\r\n          height: 300px;\r\n          width: 100%;\r\n          overflow: hidden;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}","@use \"editorPage\";\r\n@use \"horizontalResize\";\r\n@use \"button\";\r\n@use \"sidebar\";\r\n@use \"colors\" as *;\r\n\r\n@import '@vscode/codicons/dist/codicon.css';\r\n\r\n\r\n// unselectable {\r\n//   -webkit-touch-callout: none;\r\n//   -webkit-user-select: none;\r\n//   -moz-user-select: none;\r\n//   -ms-user-select: none;\r\n//   user-select: none;\r\n// }\r\n\r\nhtml {\r\n  color-scheme: dark;\r\n  min-height: 100vh;\r\n  max-height: 100vh;\r\n}\r\n\r\nbody {\r\n  background: $wm-bg1-color;\r\n  font-family: 'Spartan', sans-serif;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  overflow: hidden;\r\n  margin: 0;\r\n  font-size: 14px;\r\n}\r\n\r\n::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n/* Handle */\r\n::-webkit-scrollbar-thumb {\r\n  background: $wm-bg3-color;\r\n  border-radius: 4px;\r\n}\r\n\r\n/* Handle on hover */\r\n::-webkit-scrollbar-thumb:hover {\r\n  background: $wm-bg6-color;\r\n}\r\n\r\nkbd {\r\n  border: 1px solid $wm-bg7-color;\r\n  border-radius: 3px;\r\n}\r\n\r\n.wm-app-container {\r\n  height: 100vh;\r\n  width: 100vw;\r\n  box-sizing: border-box;\r\n\r\n  padding: 3px;\r\n\r\n  .wm-app-grid {\r\n    height: 100%;\r\n    width: 100%;\r\n    box-sizing: border-box;\r\n\r\n    display: grid;\r\n    grid-template-columns: 257px 8px calc(100vw - 257px);\r\n  }\r\n}\r\n\r\nlabel.radio {\r\n  display: flex;\r\n  gap: 6px;\r\n  padding: 2px;\r\n  box-sizing: border-box;\r\n  span {\r\n    flex-grow: 0;\r\n  }\r\n  // width: 100%;\r\n}\r\n\r\ninput[type=\"radio\"] {\r\n  appearance: none;\r\n  background-color: transparent;\r\n  margin: 0;\r\n  font: inherit;\r\n  color: $wm-bg9-color;\r\n  // display: flex;\r\n  flex-grow: 0;\r\n  box-sizing: border-box;\r\n  width: 16px;\r\n  height: 16px;\r\n\r\n  border: 2px solid $border-color;\r\n  border-radius: 50%;\r\n  place-content: center;\r\n\r\n  display: grid;\r\n  &::before {\r\n    flex-grow: 0;\r\n    content: \"\";\r\n    padding: 2px;\r\n    box-sizing: border-box;\r\n    width: 8px;\r\n    height: 8px;\r\n    border-radius: 50%;\r\n  }\r\n  &:checked {\r\n    border-color: $wm-bg6-4-color; \r\n    &::before {\r\n      background-color: $wm-bg8-color; \r\n    }\r\n  }\r\n  &:hover {\r\n    border-color: $wm-bg8-color; \r\n    &::before {\r\n      background-color: $wm-bg10-color; \r\n    }\r\n  }\r\n  &:focus {\r\n    border-color: $wm-bg9-color;\r\n    outline: none;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
